.AboutUsWrapperView {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 10px;
    margin-bottom: 50px;
}

.Title {
    font-family: var(--font-theme-primary);
    color: var(--color-theme-primary);
    text-shadow: 0px 1px 1px var(--color-shadow);
    text-transform: uppercase;
    text-align: center;
}

.Paragraph {
    font-family: var(--font-theme-secondary);
    color: var(--color-text-paragraph);
    text-align: left;
    margin: 0px;
    font-weight: 450;
}

.ExternalLink {
    font-family: var(--font-theme-secondary);
    color: var(--color-theme-secondary);
}

.LineBreak {
    border: 1px solid var(--color-theme-primary);
    width: 85%;
    margin-top: 50px;
}

.ContentBlockView {
    width: 100%;
    display: flex;
    flex-direction: row;
    margin-top: 50px;
}
.ContentViewReverse {
    flex-direction: row-reverse;
}

.ContentBlockChildren {
    display: flex;
    flex-direction: column;
    width: calc(100% - 75px);
    border-right: 1px solid var(--color-dark-transparent);
    border-left: none;
    padding-left: 15px;
    padding-right: 5px;
    overflow: hidden;
}
.ContentBlockChildrenReverse {
    border-right: none;
    border-left: 1px solid var(--color-dark-transparent);
}

.ContentIconWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 75px;
}
.ContentIcon {
    fill: var(--color-theme-primary);
    width: 20px;
    height: auto;
}

.ZigZagView{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

@media (min-width: 1280px) {
    .ContentBlockView {
        width: 50%;
        flex-direction: row-reverse; /* disable/ override reverse appearance on desktop */
    }
    .ContentBlockChildrenReverse, /* disable/ override reverse appearance on desktop */
    .ContentBlockChildren {
        border-right: none;
        border-left: 1px solid var(--color-dark-transparent);
    }
}